<template>
    <section class="gallery">
      <div class="image-container" v-for="(photo, index) in photos" :key="index">
        <img :src="photo.src" :alt="photo.title" class="gallery-image" @click="openLightbox(index)" loading="lazy">
        <div class="image-overlay">
          <h3>{{ photo.title }}</h3>
        </div>
      </div>
      <div v-if="showLightbox" class="lightbox-overlay" @click="closeLightbox">
      <div class="lightbox" :style="{ width: lightboxWidth + 'px', height: lightboxHeight + 'px' }" @click.stop>
        <img v-if="selectedPhoto" :src="selectedPhoto.src" :alt="selectedPhoto.title" class="lightbox-image">
        <button @click="closeLightbox" class="close-button"> </button>
        <button @click.stop="prevPhoto" class="nav-button prev-button">
          <i class="fas fa-chevron-left"></i>
        </button>
        <button @click.stop="nextPhoto" class="nav-button next-button">
          <i class="fas fa-chevron-right"></i>
        </button>
      </div>
    </div>
    </section>
  </template>
  
  <script>
  export default {
    name: 'Gallery',
    data() {
      return {
        photos: [
        { src: 'img/galeria/5/7.JPG', title: 'Opis obrazu 2' },
        { src: 'img/galeria/4/8.JPG', title: 'Opis obrazu 3' },
        { src: 'img/galeria/4/3.JPG', title: 'Opis obrazu 1' },
        { src: 'img/galeria/1/4.JPG', title: 'Opis obrazu 5' },
        { src: 'img/galeria/2/3.JPG', title: 'Opis obrazu 3' },     
        { src: 'img/galeria/3/3.JPG', title: 'Opis obrazu 2' },
        ],
        showLightbox: false,
        selectedPhoto: null,
        lightboxWidth: 0,
        lightboxHeight: 0,
        imageHeight: 0,
        imageWidth: 0,
        currentIndex: 0,
      };
    },
    methods: {
      openLightbox(index) {
        this.currentIndex = index;
        this.selectedPhoto = this.photos[index];
        this.calculateLightboxSize();
        this.showLightbox = true;
      },
      closeLightbox() {
        this.showLightbox = false;
      },
      nextPhoto() {
        this.currentIndex = (this.currentIndex + 1) % this.photos.length;
        this.selectedPhoto = this.photos[this.currentIndex];
        this.calculateLightboxSize();
     },
      prevPhoto() {
        this.currentIndex = (this.currentIndex - 1 + this.photos.length) % this.photos.length;
        this.selectedPhoto = this.photos[this.currentIndex];
        this.calculateLightboxSize();
      },
      calculateLightboxSize() {
        if (!this.selectedPhoto) return; 
        const screenWidth = window.innerWidth;
        const screenHeight = window.innerHeight;
        const image = new Image();
        image.src = this.selectedPhoto.src;
        image.onload = () => {
          const imageWidth = image.naturalWidth;
          const imageHeight = image.naturalHeight;
          if (imageHeight > screenHeight) {
            const ratio = screenHeight / imageHeight;
            this.lightboxHeight = screenHeight * 0.8;
            this.lightboxWidth = imageWidth * ratio;
          } else {
            this.lightboxHeight = imageHeight;
            this.lightboxWidth = imageWidth;
          }
      }
    }
  }
  };
  </script>
  
  <style scoped>

.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; 
  backdrop-filter: blur(10px);
}

.lightbox {
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
  background-color: #fff;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.lightbox-image {
  width: 100%;
  height: auto;
  display: block;
}

.close-button::before  {
  content: "\f410"; 
  font-family: FontAwesome;
  position: absolute;
  color: #bd38a4;
  top: 10px;
  right: 10px;
  border: none;
  font-size: 24px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  transition: background-color 0.3s ease;
}

  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 10px;
    background-color: #C1E1D5;
    padding: 20px;
  }
 .image-container {
  position: relative;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.image-overlay::before {
  content: "\f00e"; /* Ikona powiƒôkszania (fa-search) */
  font-family: FontAwesome;
  position: absolute;
  color: #bd38a4;
  top: 50%;
  left: 30px;
  transform: translate(-50%, -50%);
  font-size: 2rem;
}

.image-container:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}
  
  .gallery-image {
    width: 100%;
    height: 250px;
    object-fit: cover; /* Zachowaj proporcje obrazu */
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .image-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.7));
    color: #fff;
    text-align: center;
    transform: translateY(100%);
    transition: transform 0.3s ease;
  }

  .image-overlay h3 {
  font-size: 1.2rem;
  font-weight: bold;
}

.image-container:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
} 
  .image-container:hover .image-overlay {
    transform: translateY(0);
  }

  .image-container {
  transition: transform 0.3s ease;
}
  
  @media screen and (max-width: 768px) {
    .gallery {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
  }

  .nav-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #bd38a4 !important; 
  font-size: 2rem;
  transition: opacity 0.3s ease;
}

.nav-button:hover {
  opacity: 0.7;
}

.prev-button {
  left: 20px;
}

.next-button {
  right: 20px;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #fff;
  font-size: 1.5rem;
  transition: opacity 0.3s ease;
}

.close-button:hover {
  opacity: 0.7;
}

  </style>
  