<script>
import { RouterLink } from 'vue-router';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';


export default {
  name: 'Kontakt',
  components: {
    RouterLink,
    FontAwesomeIcon
  },
  methods: {
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    },
    loadFacebookSDK() {
      return new Promise((resolve) => {
        if (window.FB) {
          resolve(window.FB);
        } else {
          window.fbAsyncInit = function() {
            FB.init({
              xfbml: true,
              version: 'v20.0'
            });
            resolve(FB);
          };

          (function(d, s, id) {
            var js, fjs = d.getElementsByTagName(s)[0];
            if (d.getElementById(id)) return;
            js = d.createElement(s); js.id = id;
            js.src = "https://connect.facebook.net/pl_PL/sdk.js#xfbml=1&version=v20.0";
            fjs.parentNode.insertBefore(js, fjs);
          }(document, 'script', 'facebook-jssdk'));
        }
      });
    }
  },
  mounted() {
    window.onload = () => {
      this.loadFacebookSDK().then(FB => {
        FB.XFBML.parse();
      });
    };
  }
}
</script>

